/*--公共部分--*/
div,form,img,ul,ol,li,dl,dt,dd,p,tr,td,body,strong,span,pre,input{margin:0; padding:0; border:0; background-repeat:no-repeat;}
h1,h2,h3,h4,h5,h6,p,dl,dt,dd{ margin:0; padding:0; font-weight:normal;}
body{font-family: Arial, Helvetica, sans-serif;font-size:16px;margin:0px;padding:0px;color:#333;line-height:24px;height:auto;clear:both;}
img{border:0px;}
ul,li{ list-style:none;}
em,i{ font-style:normal;}
a{ text-decoration:none; color:#333; outline:none;}
a:hover{ background-repeat:no-repeat; color:#333; text-decoration:none;}
a,area{blr:expression(this.onFocus=this.blur())}
.clearfix:after{visibility: hidden;display: block;font-size: 0;content: " ";clear: both;height: 0;}
.clearfix{*zoom:1;}
*{-webkit-box-sizing: border-box;-moz-box-sizing: border-box;box-sizing: border-box;}

input,button,select,textarea{font-family:"微软雅黑"; outline:none !important;border:none !important;border-radius:0px !important;-webkit-appearance: none !important;-moz-appearance:none !important;}
*{ -webkit-tap-highlight-color:rgba(0,0,0,0);tap-hightlight-color:rgba(0,0,0,0); touch-callout:none; -webkit-touch-callout:none;}
